import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigIcms } from '../../../../models/dtos/mlf/configIcms';
import { DIAS_ENUM_VALUES } from '../../../../models/enumerated/ger/diasEnum';
import { CLASSE_VENCIMENTO_ENUM_VALUES } from '../../../../models/enumerated/mlf/classeVencimentoEnum';

interface ConfigIcmsProps extends EditViewProps<ConfigIcms> {}

const ConfigIcmsEditView: React.FC<ConfigIcmsProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ConfigIcms>({ defaultValues: selectedItem, mode: 'onBlur' });

  const validateExtras = formValues => {
    const erros = [];
    if (formValues.notificarVencimento === true && !formValues.notificarVencimentoDias) {
      erros.push('Dias de Antecedência do vencimento - Campo não pode estar em branco.');
    }
    return erros;
  };

  const esconderBotaoSubmit = () => {
    const padrao = formProps.getValues().padrao || false;
    return padrao.toString() === 'true';
  };

  return (
    <WindowComponent title="Configurações de ICMS">
      <EditForm
        formProps={formProps}
        editViewProps={props}
        dtoClassName={CLASS_NAME}
        validateExtras={validateExtras}
        hideSubmitButton={esconderBotaoSubmit()}
      >
        <InputGroupInline formProps={formProps} id="padrao" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 50 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição da Configuração de ICMS"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="codigoReceita"
              label="Código da Receita"
              formProps={{ ...formProps, validation: { required: true, maxLength: 5 } }}
              isPrependLabel={true}
              placeholder="Informe o código da receita"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="variacaoCodigoReceita"
              label="Variação"
              formProps={{ ...formProps, validation: onlyNumber(0, 2) }}
              tooltipProps={{ message: 'Variação do código da receita. Esse campo é obrigatório caso a empresa gere o DCTF.' }}
              isPrependLabel={true}
              placeholder="Informe a variação da Receita"
            />
          </Col>
        </Row>
        <Row>
          <Col title="Classe de Vencimento Referente a Geração do Arquivo Digital DIME-SC.">
            <InputGroupInline
              id="classeVencimento"
              label="Classe de Vencimento"
              formProps={formProps}
              isPrependLabel={true}
              inputDropDownProps={{ optionsProvider: CLASSE_VENCIMENTO_ENUM_VALUES }}
              placeholder="Informe a Classe de vencimento"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="diaVencimento"
              label="Dia de Vencimento"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o dia de vencimento"
              inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Configuração de Notificação</CardTitle>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      label="Notificar Vencimento de Imposto"
                      name="notificarVencimento"
                      id="checkNotificarVencimento"
                      innerRef={formProps.register}
                      style={{ marginBottom: '16px' }}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="notificarVencimentoDias"
                      label="Dias de Antecedência do vencimento"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe com quantos de dias de antecedência gostaria de ser notificado"
                      disabled={!formProps.getValues().notificarVencimento}
                      inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES, enabled: false }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      disabled={!formProps.getValues().notificarVencimento}
                      label="Notificar o cliente/responsável por Connect/email, sobre o vencimento do Imposto"
                      name="notificarVencimentoPorEmail"
                      id="checkNotificarVencimentoPorEmail"
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="recolhimentoMinimo"
              label="Recolhimento Mínimo"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o valor do recolhimento mínimo"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigIcmsEditView;
